* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgba(33, 33, 33, .98);
  background-size: cover;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.search-box-position {
  flex-direction: column;
  align-items: center;
  display: flex;
}

#search-box {
  font: inherit;
  width: 350px;
  text-align: center;
  border: 2px solid rgba(0, 0, 0, .16);
  border-radius: 6px;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 10px;
  font-size: 25px;
  animation: pulse 3.5s infinite;
  transform: scale(1);
  box-shadow: 3px 4px 4px #05878a;
}

#search-box:hover, #search-box:focus {
  outline: none;
  animation: none;
}

@keyframes pulse {
  0% {
    transform: scale(.9);
    box-shadow: 2px 4px 4px #05878a;
  }

  50% {
    transform: scale(1);
    box-shadow: 5px 6px 6px #82e8e6;
  }

  100% {
    transform: scale(.9);
    box-shadow: 2px 4px 4px #05878a;
  }
}

p {
  font: inherit;
  font-size: 25px;
}

